import React from "react";
import ReactTooltip from "react-tooltip";
import { Badge, Button, ButtonGroup } from "reactstrap";

const buildNotificationsColumns = (toggleNotification, selectNotification) => {
  return [
    { name: "#", selector: "id", sortable: true, width: "5%" },
    { name: "", selector: "title", sortable: true },
    { name: "", selector: "body", sortable: true },
    { name: "", selector: "time", sortable: true, width: "7%" },
    {
      name: "",
      width: "10%",
      sortable: true,
      selector: "weekdays",
      cell: row => (
        <h4>
          {
            row.weekdays === true &&
            <Badge color="warning" pill> </Badge>
          }
          {
            row.weekdays === false &&
            <Badge color="warning" pill></Badge>
          }
        </h4>
      )
    },
    {
      name: "",
      width: "10%",
      sortable: true,
      selector: "active",
      cell: row => (
        <h4>
          {
            row.active === true &&
            <Badge color="success" pill></Badge>
          }
          {
            row.active === false &&
            <Badge color="secondary" pill> </Badge>
          }
        </h4>
      )
    },
    {
      name: "",
      width: "10%",
      cell: row => (
        <center>
          <ButtonGroup className="btn-group--icons">
            { row.active === true &&
              <Button outline style={{ backgroundColor: "#9dd1fc" }} onClick={() => toggleNotification(row)}>
                <span className="lnr lnr-cross-circle" data-tip="  " />
              </Button>
            }
            { row.active === false &&
              <Button outline style={{ backgroundColor: "#9dd1fc" }} onClick={() => toggleNotification(row)}>
                <span className="lnr lnr-chevron-right-circle" data-tip=" " />
              </Button>
            }
            {
              <Button outline style={{ backgroundColor: "#9dd1fc" }} onClick={() => selectNotification(row)}>
                <span className="lnr lnr-pencil" data-tip=" " />
              </Button>
            }
          </ButtonGroup>
          <ReactTooltip />
        </center>
      )
    }
  ];
};

export {
  buildNotificationsColumns,
};
